import React from 'react';
import {
  FaEnvelope,
  FaGithub,
  FaLinkedin,
  FaPaperPlane,
  FaPlane,
  FaTwitter,
} from 'react-icons/fa';
import Input, { TextArea } from '../../elements/input';
import './contact.css';
const Contact = () => {
  return (
    <section className="container-lg pt-5">
      <div
        className="contact-header mb-sm-5"
        data-aos="fade-left"
        data-aos-duration="2000"
      >
        <div>
          <p>
            Feel free to <span className="text-primary">contact</span> me
            anytime
          </p>
        </div>
        <div>
          <h1>Get In Touch</h1>
        </div>
        <div className="contact-divider bg-primary"></div>
      </div>
      <div className="contact-body row mt-5">
        <div
          className="col-md-7 col-sm-12"
          data-aos="fade-up"
          data-aos-duration="2000"
        >
          <h4 className="text-left mb-3">Message Me</h4>
          <form action="POST" data-netlify="true">
            <div className="row mb-3">
              <div className="col-md-6 mb-3">
                <Input placeholder="Name" name="name" />
              </div>

              <div className="col-md-6">
                <Input placeholder="Email" name="email" />
              </div>
            </div>

            <div className="mb-3">
              <Input placeholder="Subject" name="subject" />
            </div>
            <div>
              <TextArea
                placeholder="Message"
                name="message"
                style={{ height: '120px' }}
              />
            </div>
            <div data-netlify-recaptcha="true"></div>
            <div>
              <button
                treatas="link"
                className="rounded-pill btn btn-primary 
            text-white px-5 py-3 mt-2 d-flex align-items-center 
            justify-content-between"
              >
                <span>Send</span> &nbsp;&nbsp;
                <FaPaperPlane />
              </button>
            </div>
          </form>
        </div>
        <div
          className="col-md-5 col-sm-12 mt-5"
          data-aos="fade-left"
          data-aos-duration="3000"
        >
          <div className="contact-info-header">
            <h4 className="text-left mb-3 ">Contact Info</h4>
            <p className="contact-para">
              Always available for freelance projects, Feel free to contact me!
            </p>
          </div>
          <ul className="list-unstyled">
            <li>
              <div className="d-flex align-items-center justify-content-start mb-2">
                <div className="contact-icons">
                  <FaEnvelope size="30" />
                </div>
                <div className="info-container">
                  <span>
                    <strong>Email</strong>
                  </span>
                  <br />
                  <span>ajaezokingsley@gmail.com</span>
                </div>
              </div>
            </li>
            <li>
              <div className="d-flex align-items-center justify-content-start mb-2">
                <div className="contact-icons">
                  <FaGithub size="30" />
                </div>
                <div className="info-container">
                  <span>
                    <strong>Github</strong>
                  </span>
                  <br />
                  <a href="https://github.com/Kingscliq">
                    https://github.com/Kingscliq
                  </a>
                </div>
              </div>
            </li>
            <li>
              <div className="d-flex align-items-center justify-content-start mb-2">
                <div className="contact-icons">
                  <FaLinkedin size="30" />
                </div>
                <div className="info-container">
                  <span>
                    <strong>LinkedIn</strong>
                  </span>
                  <br />
                  <a href="https://www.linkedin.com/in/ajaezokingsley/">
                    https://www.linkedin.com/in/ajaezokingsley/
                  </a>
                </div>
              </div>
            </li>
            <li>
              <div className="d-flex align-items-center justify-content-startmb-2">
                <div className="contact-icons">
                  <FaTwitter size="30" />
                </div>
                <div className="info-container">
                  <span>
                    <strong>Twitter</strong>
                  </span>
                  <br />
                  <a href="https://twitter.com/ajaezokingsley/">
                    https://twitter.com/ajaezokingsley/
                  </a>
                </div>
              </div>
            </li>
          </ul>
        </div>
      </div>
    </section>
  );
};

export default Contact;
