import React from "react";
import "./input.css";
const Input = ({ type, placeholder, style, onChange }) => {
  return (
    <input
      type={type}
      placeholder={placeholder}
      className="input-style"
      style={style}
      onChange={onChange}
    />
  );
};
export const TextArea = ({ placeholder, style }) => {
  return (
    <textarea
      placeholder={placeholder}
      className="input-style"
      style={style}
    ></textarea>
  );
};

export default Input;
